/* global redux_change, wp */

(function($) {
    "use strict";
    $.redux = $.redux || {};
    $(document).ready(function() {
        $.redux.wbc_importer();
    });
    $.redux.wbc_importer = function() {


		$('body').delegate('.wrap-importer.theme .theme-actions a', 'click', function(e) {
			e.stopPropagation();
		});

		$('body').delegate('.wrap-importer.theme.not-imported:not(.st-template-locked), #wbc-importer-reimport, #wbc-importer-uninstall', 'click', function(e) {
            e.preventDefault();

            var parent = jQuery(this);

            var reimport = false;

            var message = 'Import Demo Content?';

            if (e.target.id == 'wbc-importer-reimport') {
                reimport = true;
                message = 'Re-Import Content?';

                if (!jQuery(this).hasClass('rendered')) {
                    parent = jQuery(this).parents('.wrap-importer');
                }
            }
            else if(e.target.id == 'wbc-importer-uninstall') {
				message = 'All pages, posts, images imported with this demo will be deleted.\nSure you want to uninstall?';
				reimport = true;
				
                if (!jQuery(this).hasClass('rendered')) {
                    parent = jQuery(this).parents('.wrap-importer');
                }
            }
            else if(parent.hasClass('st-template-type-takeaway') ) {
                message = 'All your website content, settings and plugins will be replaced.\nSure you want to continue?';
            }

            if (parent.hasClass('imported') && reimport == false) return;

            var r = confirm(message);

            if (r == false) return;
            
			if(e.target.id == 'wbc-importer-uninstall') 
			{
				cbt_uninstall_content(parent);
				return;
            }

            if (reimport == true) {
                parent.removeClass('active imported').addClass('not-imported');
            }
            

            parent.find('.spinner').css('display', 'inline-block');

            parent.removeClass('active imported');

            parent.find('.importer-button').hide();

            var data = jQuery(this).data();
            
            // yunus edit
            var js_response = '';
            var patt = new RegExp("#js#(.*)#js#");

            data.action = "redux_wbc_importer";
            data.id = parent.attr("id");
            data.demo_import_id = parent.attr("data-demo-id");
            data.nonce = parent.attr("data-nonce");
            data.type = 'import-demo-content';
            data.wbc_import = (reimport == true) ? 're-importing' : ' ';

            // takeaway
            data.takeaway = false;
            if(parent.hasClass('st-template-type-takeaway') ) {
                data.takeaway = true;
            }

            parent.find('.wbc_image').css('opacity', '0.5');
            
            cbt_dialog('loader');
            var polling_form = new wbc_importer_iframeform(ajaxurl);
			polling_form.addParameter('action', data.action);
			polling_form.addParameter('id', data.id);
			polling_form.addParameter('demo_import_id', data.demo_import_id);
			polling_form.addParameter('nonce', data.nonce);
			polling_form.addParameter('type', data.type);
			polling_form.addParameter('wbc_import', data.wbc_import);
            polling_form.addParameter('takeaway', data.takeaway);
			
			polling_form.send(function (response)
			{
                parent.find('.wbc_image').css('opacity', '1');
                parent.find('.spinner').css('display', 'none');

                if (response.length > 0 && response.match(/Have fun!/gi)) 
                {
                    if (reimport == false) {
                        parent.addClass('rendered').find('.wbc-importer-buttons .importer-button').removeClass('import-demo-data');

                        parent.find('.theme-actions .wbc-importer-buttons').append(reImportButton);
                    }
                    parent.find('.importer-button:not(#wbc-importer-reimport)').removeClass('button-primary').addClass('button').text('Imported').show();
                    parent.find('.importer-button').attr('style', '');
                    parent.addClass('imported active').removeClass('not-imported');
                    
                } else {
                    parent.find('.import-demo-data').show();

                    if (reimport == true) {
                        parent.find('.importer-button:not(#wbc-importer-reimport)').removeClass('button-primary').addClass('button').text('Imported').show();
                        parent.find('.importer-button').attr('style', '');
                        parent.addClass('imported active').removeClass('not-imported');
                    }
                    
                    if (response.length > 0 && response.match(/id="content"/gi)) 
                    {

	                    cbt_import_poll('There was an internal server error importing demo content.<br />Most likely due to incompatible server settings such as<br />maximum execution time, maximum memory allowance etc.','error');
	                    $('.cbt-show-poll-log').click();
                    }
                    else
                    {
                    	// cbt_import_poll('There was an error importing demo content:<br />' + response.replace(/(<([^>]+)>)/gi, ""),'error');
                        cbt_import_poll('There was an error importing demo content','error');
                    }
                }
                
			});

            return false;
        });
        
        var reImportButton = '<a target="_blank" class="button wbc-importer-view-content" href="#">View Imported Pages</a><div id="wbc-importer-reimport" class="wbc-importer-buttons button-primary import-demo-data importer-button">Re-Import</div><div id="wbc-importer-uninstall" class="wbc-importer-buttons button">Uninstall</div>';
        
        $('body').on('cbt_importer-loaded', function ()
        {
        	var parent, id;
            if(cbt_imported_demos)
            {
            	$('.wrap-importer.theme.not-imported').each(function ()
            	{
    	        	parent = jQuery(this);
            		id = parent.attr('id');
            		if(typeof cbt_imported_demos[id] == 'undefined') return;
            		
            		
                    parent.addClass('rendered').find('.wbc-importer-buttons .importer-button').removeClass('import-demo-data'); 
                    parent.find('.theme-actions .wbc-importer-buttons').append(reImportButton);

                    parent.find('.importer-button:not(#wbc-importer-reimport)').removeClass('button-primary').addClass('button').text('Imported').show();
                    parent.find('.importer-button').attr('style', '');
                    parent.addClass('imported active').removeClass('not-imported');
                    
                    if(cbt_imported_demos[id].pages_url)
                    	$('a.wbc-importer-view-content', parent).attr('href', cbt_imported_demos[id].pages_url);
                    else
                    	$('a.wbc-importer-view-content', parent).remove();
            	});
            }

            $('.cbt_marketplace_container:not(.st_filterizr)').each(function ()
            {
                if($(this).find('> .filtr-item').length < 1) return;

                parent = jQuery(this);
                parent.addClass('st_filterizr');
                parent.parent().find('.st-marketplace-filters').show();

                var filterizd = $('.cbt_marketplace_container.st_filterizr').filterizr().filterizr('filter', '1');
            });

        });


       $('body').delegate('.st-only-purchased-btn', 'click', function(e) {
            if( $(this).hasClass('active') )
            {
                //$(this).removeClass('active');
                $('.st-marketplace-categories li[data-filter="1"]').click();
            } else {
                $(this).addClass('active');
            }
        });

       $('body').delegate('.st-unlock-bonus-btn', 'click', function(e) {
            e.stopPropagation();

            cbt_dialog( $('.st-marketplace-bonus-dialog-container').html() );

            $('.cbt-bonus-button').click(function ()
            {
                var val = $('.cbt-bonus-password:visible').val();
                cbt_dialog('loader');
                

                jQuery.ajax(
                {
                    url: ajaxurl,
                    type: 'post',
                    data: {action: 'cbt_unlock_bonus', password: val},
                    success: function(data)
                    {                
                        cbt_dialog(data);
                    }
                });

                return false;
            });
        });

       // $('body').delegate('.st-filter-bar-filter', 'click', function(e) {
       //      e.stopPropagation();

       //  });

        // Market place filter controls
        $('body').delegate('.st-marketplace-categories li.st-filter-bar-btn', 'click', function(e) {
            $(this).parent().find('li').removeClass('active');
            $(this).addClass('active');
        });


        
 

        // Stop user to press enter in Market place Search box
        $(".st-marketplace-search input").keypress(function(event) {
            if (event.keyCode == 13) {
                event.preventDefault();
                return false;
            }
        });
        
    };
    
    
    function cbt_uninstall_content(parent)
    {
	    var data = jQuery(parent).data();
        data.action = "cbt_uninstall_demo";
        data.demo_id = parent.attr("id");

        parent.find('.spinner').css('display', 'inline-block');
        parent.removeClass('active');
        parent.find('.wbc_image').css('opacity', '0.5');
            
        jQuery.post(ajaxurl, data, function(response) 
        {
	        parent.find('.wbc_image').css('opacity', '1');
	        parent.find('.spinner').css('display', 'none');
                
            if (response.length > 0 && response.match(/successfully uninstalled/gi)) 
            {
				alert(response);
				location.reload();
                
            } else {
                parent.addClass('active');
                alert(response);
                
            }
        });
    }


    function cbt_install_takeaway(parent)
    {
        var data = jQuery(parent).data();
        data.action = "cbt_install_takeaway";
        data.demo_id = parent.attr("id");

        parent.find('.spinner').css('display', 'inline-block');
        parent.removeClass('active');
        parent.find('.wbc_image').css('opacity', '0.5');
            
        jQuery.post(ajaxurl, data, function(response) 
        {
            parent.find('.wbc_image').css('opacity', '1');
            parent.find('.spinner').css('display', 'none');
                
            if (response.length > 0 && response.match(/successfully install/gi)) 
            {
                alert(response);
                location.reload();
                
            } else {
                parent.addClass('active');
                alert(response);
                
            }
        });
    }
    
    
	function wbc_importer_iframeform(url)
	{
	    var object = this;
	    object.time = new Date().getTime();
	    
        object.html = '<div class="cbt_importer_dialog">';
	    object.html += '<div class="cbt_loader"></div><div class="notice notice-info"><p>Please be patient. Import can take upto 10 minutes depending on your server speed.</p></div>';
	    object.html += '<div id="cbt_import_poll_msg_frame"><div id="cbt_import_poll_msg"></div></div>';
	    object.html += '<form action="'+url+'" target="iframe'+object.time+'" method="post" style="display:none;" id="form'+object.time+'"></form>';
	    object.html += '<iframe data-time="'+object.time+'" style="display:none;" id="iframe'+object.time+'" name="iframe'+object.time+'"></iframe>';
        object.html += '</div>';
	    cbt_dialog(object.html); 
	
	    object.addParameter = function(parameter,value)
	    {
	        $("<input type='hidden' />")
	         .attr("name", parameter)
	         .attr("value", value)
	         .appendTo('#form'+object.time);
	    }
	
	    object.send = function(callback)
	    {
		    cbt_import_poll_i = 0;
	        $('#form'+object.time).submit();
	        
	        cbt_import_poll('Loading importer');
	        
	        $('#iframe'+object.time).load(function()
	        {  
		        $('#cbt_popup .cbt_loader').slideUp();
		        if(callback) callback($(this).contents().find("body").html());
		        //$('#form'+$(this).data('time')).remove();  $(this).remove(); 
		        cbt_import_poll('<button class="button cbt-show-poll-log">show log</button>', 'action');   
				$('.cbt-show-poll-log').click(function() 
				{
					$('#cbt_popup iframe').css({width: '100%', height:'300px'}).slideToggle();
					return false;
				});
		    });
	    }
	}
	

    
})(jQuery);


var cbt_import_poll_i = 0;
function cbt_import_poll(msg, type)
{
	if(! type) type = 'normal';
	if(type=='action')
	{
		jQuery('#cbt_import_poll_msg').prepend('<div class="cbt_import_poll_msg cbt_import_poll_type_'+type+'">'+msg+'</div>');
		return;
	}

    if(type=='alert')
    {
        alert(msg);
        return;
    }
	
	cbt_import_poll_i++;
	jQuery('#cbt_import_poll_msg').prepend('<div class="cbt_import_poll_msg cbt_import_poll_type_'+type+'">'+cbt_import_poll_i+'. '+msg+'</div>');
}

function cbt_action_poll(action)
{
	if(action=='reload') location.reload();
}